<template>
  <div id="home">
    <!-- <template>
      <mt-header title="">
        <mt-button slot="right" @click = "gotoLogin()"></mt-button>
        </mt-header>search_not
    </template> -->

    <section class="header_search">
      <!-- <div class="user" @click="gotoAddress('myinfo',false)"><img :src="getUserAvatar"></div> -->
      <div class="search">
        <img src="../../images/myinfo/search_not.png">
        <input type="search" placeholder="...." v-model="searchStr" />
      </div>
      <i @click="gotoSearch()"></i>
    </section>

    <!--  -->
    <mt-swipe :auto="4000" id="baikeSwipe" v-if="swipeData.length">
      <mt-swipe-item v-for = "(item, index) in swipeData" class="swiperItem"><img @click = "handleSwipe(index)" :src="'//p.07358.com'+item.pic_url"></mt-swipe-item>
      <i></i>
    </mt-swipe>

    <section class="nav">
      <nav v-for="item,index in nav" @click="setNav(item.ask_type_id)">
        <div>
          <img v-if="tid==item.ask_type_id" :src="item.icon_s">
          <img v-if="tid!=item.ask_type_id" :src="item.icon">
          <span :class="tid==item.ask_type_id? 'active':'' ">{{item.name}}</span>
        </div>
      </nav>
    </section>


    <section class="baike_warp">
      <div class="left">
        <!-- <span class="active">/</span> -->
        <span v-for="item in navlist" :class="sid == item.ask_typezi_id ? 'active' : '' " @click="setNavselectd(item.ask_typezi_id)">{{item.name}}</span>
      </div>

      <div class="right">
        <ul>
          <li v-for="item in list">
            <router-link :to="{path: '/baikeDetail', query:{id: item.ask_baike_id}}">
              <BaikeCell :itemData="item"></BaikeCell>
            </router-link>
          </li>
        </ul>
      </div>
    </section>

    <!-- 50 -->
    <div class="lise_50"></div>

    <!--  tabBar -->
    <mt-tabbar fixed>
      <mt-tab-item id="" @click.native="gotoAddress('/home')"><img slot="icon" src="../../images/main/n1.png"></mt-tab-item>
      <mt-tab-item id=""><img slot="icon" src="../../images/main/n2_select.png"></mt-tab-item>
    </mt-tabbar>

  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl} from '../../components/common/mUtils'
import $ from 'webpack-zepto'
import BaikeCell from './children/baikeCell'
export default {
  data(){
    return{
      imgBaseUrl,
      userID:'',
      userInfo:null,
      isLogin:false, //
      swipeData:[],
      searchStr:'',
      list:[],
      complete: false, // 
      loading: false, // 
      pageSize:10, // 10
      sid:1,// id
      tid:1,// id
      nav:[],
      nav_icon:[
        {
          icon:'http://qpic.07358.com/Fr3wB7gyCkMg5nK4-s8z_ub8eF9I',
          icon_s:'http://qpic.07358.com/FgF9ozrpFa4_5Tsw3gG8W658bF1y'},
        {
          icon:'http://qpic.07358.com/FvUx8_tGlTnDRQvUpEinQ9y1JCPN',
          icon_s:'http://qpic.07358.com/FucNFvxFdE1afvWw4qyDPsiHfbnT'},
        {
          icon:'http://qpic.07358.com/FvLUdMO0piLVIGJfE4NprQEnRhIV',
          icon_s:'http://qpic.07358.com/FleZDpFmSE5AOHGosZJBW7CGgcdx'},
        {
          icon:'http://qpic.07358.com/FpZ-y2x-r_58L4iBGR4ouQBTKots',
          icon_s:'http://qpic.07358.com/Fti9R23nSewOsyKOE_O-BqJrLITM'},
        {
          icon:'http://qpic.07358.com/FpZ-y2x-r_58L4iBGR4ouQBTKots',
          icon_s:'http://qpic.07358.com/Fti9R23nSewOsyKOE_O-BqJrLITM'},
        {
          icon:'http://qpic.07358.com/FpZ-y2x-r_58L4iBGR4ouQBTKots',
          icon_s:'http://qpic.07358.com/Fti9R23nSewOsyKOE_O-BqJrLITM'}
      ],
      isGet:true,
      searchKey:{
        page:1,
        zid:1,
        complete:false,
      },
      navselectd:0,
      navlist:[]
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
  },
  filters:{
  },
  mounted(){
    this.initData()

    this.pageKey = this.$route.name

    // 
    this.getSwipeData()

    // 
    this.getNav()

    // 
    this.getTypeList()

    // 
    // this.getData()
  },
  components:{
    BaikeCell,
  },
  computed:{
  },
  beforeRouteEnter (to, from, next) {
     //  confirm 
     //  `this`
     // 
     if (from.name != 'AskDetail') {
       window.window.sessionStorage.removeItem('List');
       window.window.sessionStorage.removeItem('pageKey');
       window.window.sessionStorage.removeItem('scrollTop');
     }
     next()
   },
  beforeRouteLeave(to, from, next) {
     // 
     //  `this`
     if (to.name == 'AskDetail') {
        // 
        window.window.sessionStorage.scrollTop = $(window).scrollTop()
        // 
        window.window.sessionStorage.List = JSON.stringify(this.list)
        // key
        window.window.sessionStorage.pageKey = this.pageKey
        // tab
        this.searchKey.complete = this.complete
        this.searchKey.zid = this.zid
        window.window.sessionStorage.searchKey = JSON.stringify(this.searchKey)
      }
      $(window).off('scroll');
     next()
  },
  methods: {
    async initData(){
      // try{
      //   let getUrl = this.CFWConfig.APIHost + '/v3/ask/list'
      //   let result = await httpTool(getUrl);
      //   console.log(result)
      // }catch(err){
    	// 	console.log(err);
    	// }
    },
    handleSwipe(index){
      let url =  this.swipeData[index].href
      window.location.href=url;
    },
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login')
        }
      }else {
          this.$router.push(path)
      }
    },
    setNavselectd(idx){
      this.navselectd = idx
      this.sid = this.navselectd
    },
    setNav(idx){
      this.tid = idx
    },
    gotoSearch(){
      let str = encodeURI(encodeURI(this.searchStr));
      this.$router.push({path: '/search', query:{keyStr:str,type:2}})
    },
    gotoDetail(item){
      let p = {path: '/askDetail', query:{qid: item.q_id}}
      this.$router.push(p)
    },
    // 
    getData(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/baike?sid='+this.sid
      // console.log(getUrl)
      // if (this.searchKey.page == 1) {
      //   this.list = []
      // }
      this.list = []
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          if (arr.length > 0) {
            // 10
            this.complete = arr.length < 10 ? true :false
            arr.forEach((item) => this.list.push(item))
          } else {
            this.complete = true
            this.allLoaded = true
          }
        }
        this.searchKey.page++
        this.loading = false // 
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
        Indicator.close();
      });
    },
    // 
    getTypeList(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/zi?id='+this.tid
      console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.navlist = response.data.result
          this.sid = this.navlist[0].ask_typezi_id
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
        Indicator.close();
      });
    },
    // 
    getNav(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/fu'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          // this.nav = response.data.result
          let arr = response.data.result
          arr.forEach((item,idx) =>{
            item.icon = this.nav_icon[idx].icon
            item.icon_s = this.nav_icon[idx].icon_s
          })
          this.nav = arr
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
        Indicator.close();
      });
    },
    // 
    getSwipeData(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v2/adv/news?navId=600'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.status
        if (code == 200) {
          this.swipeData = response.body
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
        Indicator.close();
      });
    }
  },
  watch: {
    tid:function (val, oldVal) {
      this.getTypeList()
    },
    sid: function (val, oldVal) {
      this.getData()
    },
  }

}
</script>

<style scoped>
/**/
/*@import '../../style/common/style.css';*/
@media screen and (max-width: 1200px) {
  #baikeSwipe {height: 160px;}
}

@media screen and (max-width: 500px) {
  #baikeSwipe {height: 160px;}
}

@media screen and (max-width: 375px) {
  #baikeSwipe {height: 130px;}
}

@media screen and (max-width: 350px) {
  #baikeSwipe {height: 115px;}
}


#baikeSwipe img{
  width: 100%;
}

#baikeSwipe i{
  position: absolute;
  bottom: 2px;
  right: 2px;
  font-size: 12px;
  color: #fff;
  font-style: normal;
  padding: 3px 5px;
  background-color: rgb(140, 140, 140);
}


/*****  *****/
.header_search{
  height: 40px;
  padding: 0 8px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #f5f5f5;
}

.header_search .user{
  height: 32px;
  width: 32px;
}

.header_search .user img{
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.header_search .search{
  /*background-color: #f7f7f7;*/
  background-color: #fff;
  height: 30px;
  flex-grow: 1;
  border-radius: 3px;
  /*margin-left: 10px;*/
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 0 8px;
}

.header_search .search img{
  width: 16px;
  height: 16px;
}

.header_search .search input{
  background-color: #fff;
  font-size: 14px;
  width: 100%;
}

.header_search i{
  /*background-color: #ffc107;*/
  color: #999;
  border-radius: 5px;
  padding: 6px 6px;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
}

.nav{
  height: 80px;
  /*display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;*/
  border: 1px solid #eee;
  white-space: nowrap;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  overflow-y: hidden;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.nav nav{
  display: inline-block;
  width: 65px;
  height: 100%;
}

.nav nav div{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav nav div img{
  width: 50%;
  margin-bottom: 5px;
}

.nav nav div span{
  font-size: 14px;
}

.nav nav div span.active{
  /*color: rgb(46,142,222);*/
  color: #000;
  font-weight: 700;
}


/*****  nav_tab *****/
.nav_tab{
  height: 35px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.nav_tab i{
  font-style: normal;
  height: 35px;
  width: 98%;
  line-height: 35px;
  margin: 0 5px;
  /*border-bottom: 2px solid red;*/
}

.nav_tab .selectd{
  border-bottom: 2px solid red;
}


/*    */

@media screen and (max-width: 1200px) {
  .baike_warp{
   display: flex;
   flex-direction: row;
   height: calc(100vh - 200px);
   /*white-space: nowrap;*/
  }
}

@media screen and (max-width: 500px) {
  .baike_warp{
   display: flex;
   flex-direction: row;
   height: calc(100vh - 200px);
   /*white-space: nowrap;*/
  }
}

@media screen and (max-width: 375px) {
  .baike_warp{
   display: flex;
   flex-direction: row;
   height: calc(100vh - 200px);
   /*white-space: nowrap;*/
  }
}

@media screen and (max-width: 350px) {
  .baike_warp{
   display: flex;
   flex-direction: row;
   height: calc(100vh - 170px);
   /*white-space: nowrap;*/
  }
}

.baike_warp .left{
  width: 5.75rem;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #e5e5e5;
  min-width: 5.75rem;
  font-size: 15px;
}

.baike_warp .left span{
  width: 100%;
  height: 3.125rem;
  line-height: 3.125rem;
  /*font-weight: 700;*/
  color: #000;
}

.baike_warp .left span.active{
  color: #000;
  font-weight: 700;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
  border-right: 1px solid #fff;
  background-color: #fff;
}

.baike_warp .right{
  flex-grow: 1;
  margin-left: 20px;
  overflow-y: scroll;
  overflow-x: hidden;
}



.myPublishAsk{
  position: fixed;
  right: 10px;
  bottom: 70px;
  background-color: #31a4fc;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #fff;
  font-style: normal;
  font-weight: bold;
  border-radius: 29px;
  border: 4px solid rgba(255,255,255,0.8);
}





</style>



